const changeLangReducer = (state = initialState, action) => {
	switch (action.type) {
	case RUS:
		return {
			...state,
			langStatus: action.lang,
		};
	case ENG:
		return {
			...state,
			langStatus: action.lang,
		};
	default:
		return state;
	}
};

export const setRus = () => ({
	type: RUS,
	lang: 'rus',
});

export const setEng = () => ({
	type: ENG,
	lang: 'eng',
});

export default changeLangReducer;
